.theme-icon {
    cursor: pointer;
}

.animatable {
    will-change: transform;
    transform: translate(0, 0);
    transition: transform 400ms ease-in;
}

.is-animated {
    transform: translate(20px, -20px);
}

.dropdown-menu-sw::after {
    top: -14px;
    right: 10px;
    left: auto;
}

.dropdown-menu::after {
    border: 7px solid transparent;
    border-bottom-color: #f00;
}

.dropdown-menu-sw::after {
    top: -14px;
    right: 10px;
    left: auto;
}

.dropdown-menu::after {
    border: 7px solid transparent;
    border-bottom-color: #f00;
}

.dropdown-menu::before,
.dropdown-menu::after {
    position: absolute;
    display: inline-block;
    content: "";
}

.lists__item:before {
    content: url(./assets/line-dot.svg);
    left: 0;
    position: absolute;
    top: 0;
    transform: translateY(calc(-50% + 2.5px));
}

hr.line {
    -webkit-animation: line-anim 400s infinite;
    animation: line-anim 400s infinite;
    background: url(./assets/line-dot.svg);
    border: none;
    height: 2px;
    margin: 0;
}

.typewriter h1 {
    color: #fff;
    font-family: monospace;
    overflow: hidden;
    /* Ensures the content is not revealed until the animation */
    border-right: .15em solid orange;
    /* The typwriter cursor */
    white-space: nowrap;
    /* Keeps the content on a single line */
    margin: 0 auto;
    /* Gives that scrolling effect as the typing happens */
    letter-spacing: .15em;
    /* Adjust as needed */
    animation:
        typing 3.5s steps(30, end),
        blink-caret .5s step-end infinite;
}

/* The typing effect */
@keyframes typing {
    from {
        width: 0
    }

    to {
        width: 100%
    }
}

/* The typewriter cursor effect */
@keyframes blink-caret {

    from,
    to {
        border-color: transparent
    }

    50% {
        border-color: orange
    }
}

.hero_1::after {
    content: '';
    position: absolute;
    width: 100%;
    max-width: 100%;
    height: 1323px;
    background-image: url(./assets/svg/hero2.svg);
    background-repeat: no-repeat;
    background-size: auto;
    left: 50%;
    top: 0;
    background-position: center top;
    transform: translate(-50%);

}

.hero_image::after {
    content: '';
    position: absolute;
    width: 300%;
    max-width: 300%;
    height: 200%;
    background-image: url(./assets/svg/hero.svg);
    background-repeat: no-repeat;
    background-size: contain;
    left: 50%;
    background-position: center;
    top: 50%;
    transform: translate(-50%, -50%);
    z-index: -1;
}

.code-editor {
    border-radius: 3px;
    box-shadow: 0 48px 88px rgba(24, 35, 52, 0.32);
    will-change: transform;
    transition: all 0.45s cubic-bezier(0.215, 0.61, 0.355, 1);
    box-shadow: 24px 48px 88px rgba(24, 35, 52, 0.32);
    transform: perspective(1000px) rotateY(-13deg) rotateX(5deg) rotateZ(7deg) scaleY(0.9) scaleX(0.95) translateX(-3%) translateY(-3%);
}

.code-editor:hover {
    box-shadow: 0 48px 88px rgba(24, 35, 52, 0.32) !important;
    transform: perspective(1000px) rotateY(0deg) rotateX(0deg) rotateZ(0deg) scale(1) translateX(0) translateY(0) !important;
}